export default {
  name: '',
  components: {
  },
  data () {
    return {
      showState: true, // 是否显示底部加载更多内容
      finish: false, // 是否加载完成
      loading: false, // 是否正在加载中
      domClientHeight: 0
    }
  },
  created () {
  },
  mounted () {
    // 获取组件内容可视区域的高
    if (this.$el) {
      this.domClientHeight = window.innerHeight - this.$el.getBoundingClientRect().top
    } else {
      this.domClientHeight = window.innerHeight
    }
    this.switchBottom()
    this.bindSrcoll()
  },
  methods: {
    switchBottom () {
      this.$nextTick(() => {
        // 判断容器内容是否 大于 （网页可视区域-自身距离顶部距离）
        if (this.$el.scrollHeight > this.domClientHeight) {
          this.showState = true
        } else {
          // 如果页面不足一屏且还有下一页数据，继续执行加载更多方法
          if (!this.finish) {
            this.showState = true
            setTimeout(() => {
              this.$emit('loadMore')
            }, 1000)
          } else {
            // 否则不显示底部加载状态
            this.showState = false
          }
        }
      })
    },
    async scrollPage () {
      if (!this.$el) {
        return
      }
      var bottom = this.$el.getBoundingClientRect().bottom
      // 当内容滚动到距离底部<100时
      if (bottom - 100 < window.innerHeight && !this.loading && !this.finish) {
        this.loading = true
        setTimeout(() => {
          this.$emit('loadMore')
        }, 500)
      }
    },
    bindSrcoll () {
      this.unScroll()
      document.querySelector('.page').addEventListener('scroll', this.scrollPage)
    },
    unScroll () {
      document.querySelector('.page').removeEventListener('scroll', this.scrollPage)
    }
  },
  // 视图数据更新，重新调用
  updated () {
    this.switchBottom()
  },
  // 如果有用到keep-alive，组件激活时调用
  activated () {
    this.switchBottom()
  },
  // 页面销毁，移除滚动监听
  beforeDestroy () {
    this.unScroll()
  }
}



